.navbar{
    display: flex;
    color: white;
    justify-content: space-between;
    align-items: center;
    margin: 5px 12px;
}

.navbar-menu{
    display: flex;
    flex-direction: row;
    gap: 40px;
    align-items: center;
    justify-content: space-around;
    list-style: none;
    font-size: 18px;
    font-weight: 600;
    cursor: pointer;
}

.navbar-menu p{
    background: linear-gradient(270deg, red 20%, yellow 100%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    transition: 0.5s;
    width: 100%;
}

.navbar-menu p:hover{
    transform: scale(1.1);
    transition: 0.3s;
}

.navbar-connect{
    background: linear-gradient(270deg, red 30%, yellow 100%);
    color: black;
    border-radius: 50px;
    border: none;
    margin: 12px;
    padding: 12px;
    font-size: 18px;
    font-weight: 500;
    transition: 0.5s;
}

.navbar-connect:hover{
    border: 2px solid white;
    transform: scale(1.1);
    transition: 0.4s;
    cursor: pointer;
}

.anchor-link{
    text-decoration: none;
}