.project{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.project h1{
    font-size: 60px;
    background: linear-gradient(270deg, red -40%, yellow 140%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.project div{
    display: flex;
    text-align: left;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    font-size: 18px;
    font-weight: 500;
    transition: 0.3s;
}

.project div p span{
    color: red;
}

.github a{
    text-decoration: none;
    color: rgb(236, 91, 91);
}

.github a:hover{
    text-decoration: underline;
}