.contact{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: start;
}

.contact-here{
    font-size: 60px;
    background: linear-gradient(270deg, red 50%, yellow 106.28%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.contact-section{
    display: flex;
    flex-direction: row;
    gap: 100px;
    margin: 12px;
    padding: 30px;
}

.left-section{
    display: flex;
    flex-direction: column;
    justify-content: start;
    color: white;
    text-align: left;
    gap: 20px;
}

.left-section img{
    margin: 4px;
    margin-bottom: 0;
    margin-right: 5px;
    gap: 6px;
}

.left-section span, p{
    font-size: 20px;
    font-weight: 600;
}

.left-section h2{
    font-size: 45px;
    background: linear-gradient(270deg, red 50%, yellow 106.28%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.right-section{
    display: flex;
    flex-direction: column;
    margin-right: 100px;
    gap: 12px;
}

.right-section label{
    color: white;
    font-size: 18px;
    font-weight: 600;
    padding: 5px;
    margin: 5px;
    gap: 30px;
}

.right-section input{
    border: none;
    background-color: rgb(73, 76, 78);
    width: 300px;
    height: 50px;
    margin: 5px;
    padding: 5px;
    border-radius: 4px;
}

.right-section textarea{
    border: none;
    background-color: rgb(73, 76, 78);
    width: 300px;
    height: 150px;
    margin: 5px;
    padding: 5px;
    border-radius: 4px;
    color: white;
}

.right-section input::placeholder{
    font-weight: 600;
    margin: 2px;
    padding: 2px;
}

.right-section textarea::placeholder{
    font-weight: 600;
    margin: 2px;
    padding: 2px;
}

.submit-button{
    border: none;
    background: linear-gradient(270deg, red 40%, yellow 100%);
    color: black;
    margin: 12px;
    padding: 12px;
    border-radius: 50px;
    font-weight: 600;
    transition: 0.5s;
    width: 50%;
    font-size: 16px;
}

.submit-button:hover{
    border: 2px solid white;
    transition: 0.3s;
    transform: scale(1.1);
}