.hero{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px;
    margin: 50px;
}

.hero-profile img{
    height: 400px;
    width: 100%;
}

.hero-desc{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.hero-desc{
    font-size: 30px;
    font-weight: 600;
    text-align: center;
}

.hero-desc span{
    background: linear-gradient(270deg, red 40%, yellow 100%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.hero-desc h1, p{
    color: white;
    width: 75%;
}

.hero-desc p{
    font-weight: 500;
}

.button-type{
    display: flex;
    flex-direction: row;
    gap: 40px;
}

.hero-connect{
    background: linear-gradient(270deg, red 40%, yellow 100%);
    color: black;
    margin: 12px;
    padding: 12px;
    border-radius: 50px;
    font-weight: 600;
    transition: 0.5s;
}

.hero-connect:hover{
    border: 2px solid white;
    transition: 0.3s;
    transform: scale(1.1);
}

.hero-resume{
    color: white;
    border: 2px solid yellow;
    border-radius: 50px;
    padding: 12px;
    margin: 12px;
    font-weight: 600;
    a{
        text-decoration: none;
        color: white;
    }
}

.hero-resume:hover{
    border: 2px solid white;
    color: yellow;
    transition: 0.3s ease-in-out;
    transform: scale(1.1);
    a{
        text-decoration: none;
        color: yellow;
    }
}