.footer{
    display: flex;
    flex-direction: column;
}

.footer-top{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 0;
    margin-top: 20px;
}

.footer-top p{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.footer-bottom{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 2px;
    padding: 2px;
    margin-top: 0;
}

.footer-bottom p{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

hr{
    width: 100%;
}