.about{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.about h1{
    font-size: 60px;
    background: linear-gradient(270deg, red -40%, yellow 140%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.info{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.para{
    display: flex;
    text-align: center;
    justify-content: center;
    align-items: center;
    width: 75%;
    font-size: 24px;
    font-weight: 600;
}

.skills p{
    display: flex;
    text-align: center;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    font-size: 18px;
    font-weight: 500;
    transition: 0.3s;
}

.skill:hover{
    transform: scale(1.05);
    transition: 0.3s;
}